export default {
	data() {
		return {
			gender:'',
			items:[{name:'man',value:'男'},{name:'woman',value:'女'}],
			latestDate: '2019-6-25',
			feature:'',
			FindProcess:'',
			otherInfo:'',
			otherDescription:'',
			imgList: []
		};
	},
	methods:{
		radioChange(e) {
			this.gender=e.detail.value;
		},
		LatestDateChange(e) {
			this.findDate = e.detail.value
		},
		FindInput(e) {
			this.FindProcess = e.detail.value
		},
		featureInput(e){
			this.feature = e.detail.value
		},
		otherInfoInput(e){
			this.otherInfo = e.detail.value
		},
		otherDescriptionInput(e){
			this.otherDescription = e.detail.value
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '寻子者',
				content: '确定要删除这张照片吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		submit(e){
			
		}
	}
};
